<template>
	<view></view>
</template>

<script>
	import api from '../../api/api.js'
	export default {
		onShow () {
			let self = this
			uni.getStorage({
				key: 'mine',
				success: (r) => {
					let _res = JSON.parse(r.data)
					self.init(_res)
					uni.switchTab({
						url: '../mine/mine'
					})
				}
			})
		},
		methods: {
			init (_res) {
				let self = this
				/** 获取登陆者信息 */
				getApp().globalData.mine = _res
				
				/** 获取朋友 */
				self.getFirends(_res)
				
				/** 获取好友申请  (后续后端改为一周之内 分页 的信息) */
				self.getApply(_res)
				
				/** 获取聊天列表 */
				self.getFriendChat(_res)
				
				self.socketEmit('add_socket', {
					data: _res.username
				})
				
				self.socketOn('addFriend', (_) => {
					getApp().globalData.friends.push(_.data[0])
					// 手机震动
					uni.vibrateLong()
				})
				self.socketOn('addApply', (_) => {
					getApp().globalData.applys.push(_.data[0])
					// 手机震动
					uni.vibrateLong()
				})
			},
			/**
			 * 获取 pass_state = 2 朋友
			 * @param { friend_uid 用户自己的id } _res
			 */
			getFirends (_res) {
				let self = this
				api.getApply({
					friend_uid: _res.username,
					pass_state: 2,
					uid: ''
				})
				.then(r => {
					let _r = r[1].data
					if (_r.code == 200) {
						getApp().globalData.friends = _r.data
					}
				})
				.catch(ret => {
					console.log(ret)
				})
			},
			/**
			 * 获取好友申请
			 * @param { friend_uid 用户自己的id } _res
			 */
			getApply (_res) {
				let self = this
				api.getApply({
					friend_uid: _res.username,
					pass_state: '',
					uid: ''
				})
				.then(r => {
					let _r = r[1].data
					if (_r.code == 200) {
						getApp().globalData.applys = _r.data
					}
				})
				.catch(ret => {
					console.log(ret)
				})
			},
			/** 获取好友消息列表 */
			getFriendChat (_res) {
				let self = this
				api.getFriendChat({
					friend_uid: _res.username
				})
				.then(r => {
					let _r = r[1].data
					if (_r.code == 200) {
						_r.data.forEach((e, i) => {
							getApp().globalData.catting['friend-' + e.uid] = e
							self.socketOn('friend-' + e.uid, (_) => {
								api.getMessage({
									cid: _.data
								})
								.then(res => {
									let _res = res[1].data
									if (_res.code == 200) {
										getApp().globalData.catting['friend-' + e.uid].chatting.push(_res.data[0])
										// 手机震动
										uni.vibrateLong()
									}
								})
							})
						})
					}
				})
			}
		}
	}
</script>
